import React from "react";
import Context from "../../../Context";
import { injectIntl } from "react-intl";
import { Row, Col } from "antd";
import key from "../../../util/key";
import { SearchOutlined } from "@ant-design/icons";
import DataTable from "../../../components/Datatable/DataTable";
import DataTablePeriod from "../../../components/Datatable/DataTablePeriod";
import DataTableSearch from "../../../components/Datatable/DataTableSearch";
import { DatabaseOutlined } from "@ant-design/icons";
import Title from "../Title";

class DbLog extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],
    };
  }
  m = (key, prefix = "admin.dblog.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };
  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  setColumns = () => {
    this.setState({
      columns: [
        {
          title: "id",
          dataIndex: "id",
          sorter: true,
          render: (value) => <>{value}</>,
          width: "1%",
        },
        {
          title: this.m("datetime"),
          dataIndex: "datetime",
          sorter: true,
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br />
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },
        {
          title: this.m("type"),
          dataIndex: "type",
          sorter: true,
          render: (value, item) => (
            <>
              {value} {item.typename}
            </>
          ),
        },
        {
          text: true,
          title: this.m("username"),
          dataIndex: "username",
          sorter: true,
          render: (value) => (
            <div style={{ whiteSpace: "nowrap" }}>{value}</div>
          ),

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{ color: filtered ? "#1890ff" : undefined }}
            />
          ),
        },
        {
          title: this.m("session"),
          dataIndex: "session",
          sorter: true,
        },
        {
          title: this.m("code"),
          dataIndex: "code",
          sorter: true,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{ color: filtered ? "#1890ff" : undefined }}
            />
          ),
        },

        {
          title: this.m("event"),
          dataIndex: "event",
          sorter: true,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{ color: filtered ? "#1890ff" : undefined }}
            />
          ),
        },
        {
          title: this.m("text"),
          dataIndex: "text",
          sorter: true,
          width: "30%",
          render: (value) => (
            <div
              style={{
                wordBreak: "break-all",
                whiteSpace: "break-spaces",
                maxWidth: "100%",
              }}
            >
              {value}
            </div>
          ),
        },
      ],
    });
  };

  render() {
    return (
      <div>
        <Title
          title={this.m("dblog")}
          url="/admin/dblog"
          icon={<DatabaseOutlined />}
        />

        <Row>
          <Col span={24}>
            <DataTable
              uuid={this.state.tableUuid}
              columns={this.state.columns}
              path={"/admin/event/log/list"}
            />
          </Col>
        </Row>
      </div>
    );
  }
}

export default injectIntl(DbLog);
